import React from "react";
import { useState } from "react";

const Content = () => {
  const [articles, setArticles] = useState([
    {
      id: 0,
      imp: "1",
      cit: "0",
      nath: "1",
      place: "2",
      type: "0",
      role: "first",
    },
  ]);

  const addArticle = () => {
    const oldarticles = [...articles];
    let theid = 0;
    if (oldarticles[oldarticles.length - 1]) {
      theid = oldarticles[oldarticles.length - 1].id + 1;
    }
    const newarticles = [
      ...oldarticles,
      {
        id: theid,
        imp: "1",
        cit: "0",
        nath: "1",
        place: "2",
        type: "0",
        role: "first",
      },
    ];
    setArticles(newarticles);
  };

  const onDeleteItem = (id) => {
    const oldarticles = [...articles];
    const newarticles = oldarticles.filter((item) => {
      return item.id !== id;
    });
    setArticles(newarticles);
  };

  const onChangeValue = (event, id, key) => {
    let oldarticles = [...articles];
    const index = oldarticles.findIndex((item) => {
      return item.id === id;
    });
    console.log(index, id);
    let newOne = oldarticles[parseInt(index)];
    console.log(newOne);
    newOne[key.toString()] = event.target.value;
    oldarticles.splice(index, 1, newOne);
    setArticles(oldarticles);
  };


  const Box1 = (article_type) => {
    let newArticleType = 0.1;
    switch (parseInt(article_type)) {
      case 0:
      case 1:
      case 2:
      case 3:
      case 4:
      case 5:
        newArticleType = 0.2;
        break;
      case 10:
      case 11:
      case 12:
      case 13:
      case 14:
      case 15:
      case 16:
        newArticleType = 0.1;
        break;
      default:
        newArticleType = 0.1;
        break;
    }

    return newArticleType
  }


  const Box2 = (N) => {
    const result = (0.1) / (1 + Math.log10(parseInt(N)));
    return result;
  }

  const Box3 = (R) => {
    const result = (0.2) / (1 + Math.log10(parseInt(R)));
    return result;
  }

  const Box4 = (S) => {
    const result = (0.2 * S) / (1.6 + S);
    return result;
  }

  const Box5 = (C) => {
    const result = (0.3 * C) / (50 + C);
    return result;
  }

  const singleCalc = (imp, cit, author_n, place, articleType, role) => {

    const S = parseFloat(imp);
    const C = parseInt(cit);

    // Change N BY Role
    let N = parseInt(author_n);
    if (role === "first" || role === "coress" || role === "last") {
      N = 1;
    }

    // Change R By N
    let R = 1; //Default

    if (N < 100) {
      if (role === "first" || role === "coress" || role === "last") {
        R = 1;
      }
      if (role === "co") {
        R = parseInt(place);
      }
    } else {
      if (role === "first" || role === "coress" || role === "last") {
        R = 1;
      }
      if (role === "co") {
        R = 99;
      }
    }


    // Box 1
    const T = Box1(articleType);

    // Box 2
    const Box2Value = Box2(N);

    // Box 3
    const Box3Value = Box3(R);

    // Box 4
    const Box4Value = Box4(S);

    // Box 5
    const Box5Value = Box5(C);

    // Main Result
    const mainResult = T + Box2Value + Box3Value + Box4Value + Box5Value;


    return mainResult;
  };

  const mainCalcFunc = () => {
    //Do Sigma
    let sigma = 0.0;
    let iterate_count = 0;
    articles.forEach((item) => {
      sigma =
        sigma +
        singleCalc(
          item.imp,
          item.cit,
          item.nath,
          item.place,
          item.type,
          item.role
        );
      iterate_count = iterate_count + 1;
    });
    console.log(sigma);

    let mean = (sigma / iterate_count);
    let new_ur_index = (sigma * mean);
    document.getElementById("result").innerHTML = parseFloat(new_ur_index);
    document.getElementById("mean").innerHTML = parseFloat(mean);
  };

  const articlesList = articles.map((item) => {
    let ableToChoosePlace = false;
    if (item.role === "first" || item.role === "coress" || item.role === "last") {
      ableToChoosePlace = false;
    }
    if (item.role === "co") {
      ableToChoosePlace = true;
    }
    return (
      <li key={item.id}>
        <div className="listitem listitem-imp">
          <input
            value={item.imp}
            onChange={(e) => onChangeValue(e, item.id, "imp")}
          />
        </div>
        <div className="listitem listitem-cit">
          <input
            value={item.cit}
            onChange={(e) => onChangeValue(e, item.id, "cit")}
          />
        </div>
        <div className="listitem listitem-nath">
          <input
            value={item.nath}
            onChange={(e) => onChangeValue(e, item.id, "nath")}
          />
        </div>
        <div className="listitem listitem-role">
          <select
            value={item.role}
            onChange={(e) => onChangeValue(e, item.id, "role")}
          >
            <option value="first">1st author</option>
            <option value="coress">Correspondence</option>
            <option value="co">Co-author</option>
            <option value="last">Last author</option>
          </select>
        </div>
        <div className="listitem listitem-place">
          <input
            value={item.place}
            onChange={(e) => onChangeValue(e, item.id, "place")}
            disabled={!ableToChoosePlace}
            type="number"
            min={2}
          />
        </div>
        <div className="listitem listitem-type">
          <select
            value={item.type}
            onChange={(e) => onChangeValue(e, item.id, "type")}
          >
            {/* <option value="0">Editorial</option>
            <option value="1">Letter</option>
            <option value="2">Short report</option>
            <option value="3">Case Report</option>
            <option value="4">Original‌ Article</option>
            <option value="5">Review Article</option>
            <option value="6">Meta-analysis</option>
            <option value="7">Book Chapter</option> */}
            <option value="0">Original‌ Article</option>
            <option value="1">Review Article</option>
            <option value="2">Book Chapter</option>
            <option value="3">Book</option>
            <option value="4">Data Paper</option>
            <option value="5">Short Survey</option>
            {/* ==================================== */}
            <option value="11">Letter</option>
            <option value="10">Editorial</option>
            <option value="13">Case Report</option>
            <option value="12">Short Report</option>
            <option value="15">Note</option>
            <option value="14">Meta-analysis</option>
            <option value="16">Conference Paper</option>
          </select>
        </div>
        <div className="listitem listitem-tools">
          <div className="delete-item" onClick={() => onDeleteItem(item.id)}>
            ×
          </div>
        </div>
      </li>
    );
  });
  return (
    <div className="content">
      <div className="list">
        <li>
          <div className="listitem listitem-imp listitem-header">
            Impact Factor
          </div>
          <div className="listitem listitem-cit listitem-header">
            Number Of Citation
          </div>
          <div className="listitem listitem-nath listitem-header">
            Number Of Authors
          </div>
          <div className="listitem listitem-role listitem-header">Role</div>
          <div className="listitem listitem-place listitem-header">
            Place Of Author
          </div>
          <div className="listitem listitem-type listitem-header">
            Article Type
          </div>
          <div className="listitem listitem-tools listitem-header"></div>
        </li>

        {articlesList}
        <div className="add-item">
          <div className="add-btn" onClick={addArticle}>
            +
          </div>
        </div>
      </div>
      <hr />
      <div className="calc" onClick={mainCalcFunc}>
        Calculate UR-Index
      </div>
      <div className="result">
        <span>
          Result: <b id="result"></b>
          <br />
          Mean : <b id="mean"></b>
        </span>
      </div>
    </div>
  );
};

export default Content;
